.donate {
  color: #fff;
  min-height: 100vh;
  background: #1976d2;  
  background-image: radial-gradient(
    circle farthest-side at center bottom,
    #1976d2,
    #8b73d8 125%
  );
  position: flex;
}

.money {
  background-color: #009cde;
  background-image: radial-gradient(
    circle farthest-side at center bottom,
    #009cde,
    #003087 125%
  );
  color: #fff;
  min-height: 100vh;
  position: flex;
}

.crypto {
  background-color: #009cde;
  background-image: radial-gradient(
    circle farthest-side at center bottom,
    #424242,
    #212121 125%
  );
  color: #fff;
  min-height: 100vh;
  position: flex;
}

.donate-body {
}

.donate > .donate-body > .donate-txt-body > .logo {
  display: inline-flex;
  padding: 8px;
  border: 2px solid #fff;
  border-radius: 100rem;
  margin-bottom: 15px;
}

.donate > .donate-body > .donate-txt-body > .logo > img {
  border-radius: 100rem;
  height: 150px;
  padding: 5px;
  background: #fff;
}

.donate > .donate-body > .donate-txt-body > .methods > .link-donate-method {
  color: #fff;
  border-radius: 5rem;
  display: inline-block;
  font-weight: 400;
  vertical-align: middle;
  background-color: transparent;
  border: 1px solid #fff;
  padding: 0.375rem 0.8rem !important;
  text-decoration: none !important;
  font-size: 1rem;
  line-height: 1.5;
  margin-left: 11px !important;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
    border-color 0.15s ease-in-out;
}

.donate
  > .donate-body
  > .donate-txt-body
  > .methods
  > .link-donate-method
  > svg {
  margin-right: 5px;
  height: 1.3rem !important;
  width: 1.3rem !important;
  padding-bottom: 0px;
  margin-top: -4px;
}

.donate
  > .donate-body
  > .donate-txt-body
  > .methods
  > .link-donate-method
  > svg,
.donate
  > .donate-body
  > .donate-txt-body
  > .methods
  > .link-donate-method
  > svg
  > path {
  fill: #fff !important;
}

.donate > .donate-body > .donate-txt-body > .methods > .link-donate-method {
  color: #fff;
  border-radius: 5rem;
  display: inline-block;
  font-weight: 400;
  vertical-align: middle;
  background-color: transparent;
  border: 1px solid #fff;
  padding: 0.375rem 0.8rem !important;
  text-decoration: none !important;
  font-size: 1rem;
  line-height: 1.5;
  margin-left: 11px !important;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
    border-color 0.15s ease-in-out;
}

.donate
  > .donate-body
  > .donate-txt-body
  > .methods
  > .link-donate-method:hover {
  background: space;
  background-color: rgba(255, 255, 255, 0.3);
  cursor: pointer;
}

.methods {
  padding-top: 2rem;
}

.methods > h5 {
  margin-bottom: 15px;
}

.pay-methods {
  margin-top: 80px !important;
  display: flex !important;
  flex-direction: column !important;
  min-width: 0 !important;
  word-wrap: break-word !important;
  background-color: #fff !important;
  background-clip: border-box !important;
  border: 0.0625rem solid rgba(231, 234, 243, 0.7) !important;
  border-radius: 0.75rem !important;
  box-shadow: 0 6px 12px rgba(140, 152, 164, 0.075) !important;
}

.link-donate-pay-method {
  display: block;
  color: rgba(0, 0, 0, 0.35);
  border-radius: 1.3rem;
  font-weight: 400;
  vertical-align: middle;
  background-color: transparent;
  padding: 1.2rem 0rem !important;
  text-decoration: none !important;
  font-size: 1rem;
  line-height: 1.5;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
    border-color 0.15s ease-in-out;
  width: 80%;
  margin-right: 10%;
  margin-left: 10%;
  align-content: center;
  text-align: left;
}

.link-donate-pay-method > .title {
  color: #000 !important;
  padding: 0.25rem;
  vertical-align: middle;
  position: relative;
  display: inline-block;
  font-weight: bold;
}

.link-donate-pay-method > svg {
  height: 2rem !important;
  width: 2rem !important;
  margin-right: 0.8rem;
  display: inline-block;
}

.link-donate-pay-method > svg,
.link-donate-pay-method > svg > path {
  fill: #000;
}

.btn-select {
  float: right;
  display: inline-block;
}

.btn-select > .link-donate-method {
  color: #000;
  border-radius: 5rem;
  display: inline-block;
  font-weight: 400;
  vertical-align: middle;
  background-color: transparent;
  border: 1px solid #fff;
  padding: 0.375rem 0.8rem !important;
  text-decoration: none !important;
  font-size: 1rem;
  line-height: 1.5;
  margin-left: 11px !important;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
    border-color 0.15s ease-in-out;
}

.btn-select > .link-donate-method:hover {
  background: rgba(255, 255, 255, 0.3);
}

.paypal > .btn-select > .link-donate-method {
  background-color: #009cde;
  color: #ffffff;
}

.paypal > .btn-select > .link-donate-method:hover {
  background-color: #0072bc;
  color: #ffffff;
}

.pay-methods {
  padding: 30px 15px;
}

.pay-methods > h5 {
  color: #000;
}

.back-to-page {
  display: block;
  position: absolute;
  min-width: max-content;
  color: #fff;
  border-radius: 5rem;
  font-weight: 400;
  padding: 0.375rem 0.8rem !important;
  text-decoration: none !important;
  font-size: 1rem;
  cursor: pointer;
  line-height: 1.5;
  margin-left: 11px !important;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out;
}

.back-to-page > svg
{
  margin-bottom: 2px;
}

.back-to-page > svg > path
{
  fill: #fff !important;
}

.back-to-page:hover {
  color: #fff;
  background: rgba(255, 255, 255, 0.2);
}

.container {
  min-width: 800px !important;
}

.terms
{
  font-size: 0.75rem;
  text-align: center;
  padding-top: 10px;
  opacity: 0.6;
}

