.motion {
  background: linear-gradient(
    120deg,
    rgba(69, 246, 176, 1) 0%,
    rgba(88, 178, 240, 1) 33%,
    rgba(208, 135, 252, 1) 66%,
    rgba(180, 87, 217, 1) 100%
  ); /* #45f6b0 #58b2f0 #d087fc #b457d9 */
  background-size: 400% 400%;
  animation: gradient-move 60s ease infinite;
}

.banner {
  margin-top: 30px;
