}

.linked-in {
  background: linear-gradient(
    120deg,
    /*rgba(88, 178, 240, 1) 0%,*/ rgba(0, 157, 224, 1) 0%,
    rgba(10, 102, 194, 1) 50%,
    rgba(0, 157, 224, 1) 100% /*,
    rgba(88, 178, 240, 1) 100%*/
  );
  background-size: 400% 400%;
  animation: gradient-move 40s ease infinite;
}

.motion-top {
  background: linear-gradient(
    120deg,
    rgba(69, 246, 176, 1) 0%,
    rgba(88, 178, 240, 1) 33%,
    rgba(208, 135, 252, 1) 66%,
    rgba(180, 87, 217, 1) 100%
  );
  background-size: 400% 400%;
  animation: gradient-move 40s ease infinite;
}

